    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				memberInfo:[],
				pp_state:true
			}				
		},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
        methods: {  
            ...mapMutations(['logout','setUserInfo']),  
            async loadData() {  
				var _this = this;
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'../login',
					})
                }else{
					_this.$Request.post('member_account').then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.setUserInfo(res.datas);							
							_this.memberInfo = res.datas;
							_this.pp_state = res.datas.p_state;
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'../login',
							});
						}
					});
				}
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			toSetPPay: function() {
				let url = '/pages/member/set/ppassword';
				if(!this.pp_state){
					url = '/pages/member/set/getppwd';
				}
				uni.navigateTo({
					url:url,
				})
			},
        }  
    }  
